document.addEventListener('DOMContentLoaded', function() {
    const switchInputs = document.querySelectorAll('.switch input[type="checkbox"]');

    switchInputs.forEach(function(switchInput) {
        const switchElement = switchInput.parentElement;

        if (switchInput.checked) {
            switchElement.classList.add('is-checked');
        }

        switchInput.addEventListener('change', function() {
            if (this.checked) {
                switchElement.classList.add('is-checked');
            } else {
                switchElement.classList.remove('is-checked');
            }
        });
    });
});
